* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #fff;
  color: #424242;
  line-height: 1.5;
  font-size: 22px;
}

ul,
p,
h2 {
  margin: 0;
  padding: 0;
}

.wrapper {
  padding: 2.25em 1.6875em;
  background-image: -webkit-repeating-radial-gradient(
    center center,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 1px,
    transparent 1px,
    transparent 100%
  );
  background-image: -moz-repeating-radial-gradient(
    center center,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 1px,
    transparent 1px,
    transparent 100%
  );
  background-image: -ms-repeating-radial-gradient(
    center center,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 1px,
    transparent 1px,
    transparent 100%
  );
  background-image: repeating-radial-gradient(
    center center,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 1px,
    transparent 1px,
    transparent 100%
  );
  -webkit-background-size: 8px 8px;
  -moz-background-size: 8px 8px;
  background-size: 8px 8px;

  width: 100%;
  height: 100vh;
}

.container {
  max-width: 700px;
  padding: 15px;
  margin: 0 auto;
}

.input {
  border: 3px solid #bdbdbd;
  border-radius: 5px;
  padding: 10px;
  font-size: 28px;
  width: 650px;
  margin-bottom: 20px;
}

.countries-list {
  margin-left: 25px;
}

.country-title {
  font-size: 34px;
  text-align: center;
  margin-bottom: 20px;
}

.country-box {
  display: flex;
  justify-content: space-between;
}

.country-detail {
  font-weight: 600;
}

.detail {
  font-weight: 400;
}

.language-detail {
  margin-left: 25px;
}
